.PokemonList {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 20px;
}

.PokemonList div {
    margin-bottom: 10px;
    background-color: white;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.PokemonList a {
    margin-left: 10px;
    text-decoration: none;
    color: #0077cc;
}

.PokemonList a:hover {
    text-decoration: underline;
}

.PokemonList button {
    margin-left: 10px;
    background-color: #0077cc;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 10px;
    cursor: pointer;
}